const axios = require('axios');

const url = 'https://emirmertoglu-ileti-takip.herokuapp.com/api/iletiler/';

class iletiService{

    static iletileriGetir(){
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(url);
                const data = res.data;
                resolve(
                    data.map(ileti => ({
                        ...ileti,
                        olusturulmaTarihi: ileti.olusturulmaTarihi
                    }))
                )
            } catch (err) {
                reject(err);
            }
        })
    }

    static iletiEkle(ileti){
        return axios.post(url, {
            ileti: ileti
        })
    }

    static iletiSil(id){
        return axios.delete(`${url}${id}`);
    }

    static iletiGuncelle(id, guncellenmisIleti){
        return axios.put(`${url}${id}`, {
            ileti: guncellenmisIleti
        })
    }
}

export default iletiService;