<template>
  <div class="container">
    <div class="iletiOlustur">
      <label for="iletiOlustur">İleti: </label>
      <input type="text" id="iletiOlustur" v-model="yeniIleti" placeholder="Gönderilecek iletinizi yazın..">
      <button @click="gonder">Gönder</button>
    </div>
    <hr>
    <h2>Son İletiler</h2>
    <hr>
    <p class="error" v-if="error">{{error}}</p>
    <div>
      <div class="post" v-for="ileti in iletiler" v-bind:key="ileti._id">
        {{ileti.olusturulmaTarihi}}
        <p class="ileti">{{ileti.ileti}}</p>
        <button @click="sil(ileti._id)">Sil</button>
        <button @click="gorunum = !gorunum">İletiyi Güncelle</button>
        <div v-show="gorunum">
          <input type="text" id="iletiOlustur" v-model="gncIleti" placeholder="Güncellenecek iletinizi yazın..">
          <button @click="guncelle(ileti._id)">Güncelle</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import iletiService from '../iletiService';
export default {
  name: 'iletiComponent',
  data() {
    return{
      iletiler: [],
      error: '',
      yeniIleti: '',
      gorunum: false,
      gncIleti: ''
    }
  },
  async created() {
    try {
      this.iletiler = await iletiService.iletileriGetir();
    } catch (err) {
      this.error = err.message;
    }
  },
  methods: {
    async gonder(){
      await iletiService.iletiEkle(this.yeniIleti);
      this.iletiler = await iletiService.iletileriGetir();
    },
    async sil(id){
      await iletiService.iletiSil(id);
      this.iletiler = await iletiService.iletileriGetir();
    },
    async guncelle(id){
      await iletiService.iletiGuncelle(id, this.gncIleti);
      this.iletiler = await iletiService.iletileriGetir();
    }
  }
}
</script>

